@import "../colors.scss";
@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap");

.header-component {
  text-align: center;
  color: $white;

  .name-title {
    font-family: "Comfortaa", cursive;
    font-size: 15vw;
    color: $blue-border;
  }

  #logo {
    width: 90vw;
    overflow: visible;
    path:nth-child(1) {
      stroke-dasharray: 1150.4031982421875px;
      stroke-dashoffset: 1150.4031982421875px;
      animation: line-anim 3s ease forwards;
    }
    path:nth-child(2) {
      stroke-dasharray: 1058.04052734375px;
      stroke-dashoffset: 1058.04052734375px;
      animation: line-anim 3s ease forwards 0.2s;
    }
    path:nth-child(3) {
      stroke-dasharray: 783.5400390625px;
      stroke-dashoffset: 783.5400390625px;
      animation: line-anim 3s ease forwards 0.4s;
    }
    path:nth-child(4) {
      stroke-dasharray: 1058.04052734375px;
      stroke-dashoffset: 1058.04052734375px;
      animation: line-anim 3s ease forwards 0.6s;
    }
    path:nth-child(5) {
      stroke-dasharray: 1215.2392578125px;
      stroke-dashoffset: 1215.2392578125px;
      animation: line-anim 3s ease forwards 0.8s;
    }
    path:nth-child(6) {
      stroke-dasharray: 375.3000793457031px;
      stroke-dashoffset: 375.3000793457031px;
      animation: line-anim 3s ease forwards 1s;
    }
    path:nth-child(7) {
      stroke-dasharray: 990.6376342773438px;
      stroke-dashoffset: 990.6376342773438px;
      animation: line-anim 3s ease forwards 1.2s;
    }

    @keyframes line-anim {
      to {
        stroke-dashoffset: 0;
      }
    }
  }
}
